@import url('https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap');

body {
  background: #121213;
  font-family: 'Roboto Slab', serif;
}

.logos {
  height: 100px;
  border-bottom: 1px solid #333;
  margin-bottom: 50px;
  display: flex;
  justify-content: space-around;
}

.topHeader {
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid #333;

  .insideSlider {
    height: 50vh;

    .item {
      width: 100%;
      height: 50vh;
      background: black;

      video {
        width: 100%; height: 100%;
        position: absolute;
        left: 50%; top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  .overlay {
    color: white;
    position: absolute;
    bottom: 20px;
    font-size: 1.75em;
    width: calc(100% - 40px);
    text-align: center;
    padding: 20px;
    // transform: translate(0, -50%);
  }
}

.content {
  display: flex;
  margin-bottom: 10vh;

  .left, .right {
    width: 50%;
    color: white;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    line-height: 25pt;
    font-size: 1.25em;
    letter-spacing: 0.5pt;
    word-spacing: 3pt;

    p, ul {
      max-width: 600px;
      margin: 20px;
      margin-top: 0;

      a {
        color: white;
        text-decoration: none;
        background: #333;
        padding-left: 10px;
        padding-right: 10px;

        &:hover {
          background: white;
          color: #333;
        }
      }
    }
  }

  .right {
    position: relative;
    .canvas, .output {
      background: #333;
      width: 256px;
      height: 256px;
      display: inline-block;
      margin: 5px;
      position: relative;
    }

    .inside.hidden {
      display: none;
    }

    .loading {
      font-size: 0.75em;
      color: #999;
    }

    .info {
      text-align: left;
      margin-bottom: 50px;
    }

    .inside {
      text-align: center;
      max-width: 600px;
      width: 100%;
    }

    .nav {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 5px;
      margin-bottom: 50px;

      .itemBlock {
        text-transform: uppercase;
        font-size: 0.75em;
        text-align: center;
        background: transparent;
        color: white;
        border: 1px solid white;
        cursor: pointer;

        &:hover {
          background: #333;
          cursor: pointer;
        }

        &.active {
          background: #fcfcfc;
          color: #333;
        }
      }
    }
  }

  .paleta {
    position: absolute;
    width: 20px;
    left: -35px;

    .color {
      width: 20px;
      height: 20px;
      margin-bottom: 5px;
      border-radius: 40px;
      background: white;
      cursor: pointer;
      border: 3px solid white;
      transform: translateX(0px);
      transition: 0.2s all ease;
  
      &.active {
        transform: translateX(-15px);
      }
    }

    &.luna {
      .color:nth-child(1){
        background: rgb(255, 0, 0);
      }

      .color:nth-child(2){
        background: rgb(0,255,0);
      }

      .color:nth-child(3){
        background: rgb(0,0,255);
      }

      .color:nth-child(4){
        background: rgb(0,0,0);
      }
    }

    &.topo {
      .color:nth-child(1){
        background: rgb(142,181,219);
      }

      .color:nth-child(2){
        background: rgb(212,197,184);
      }

      .color:nth-child(3){
        background: rgb(239,223,193);
      }

      .color:nth-child(4){
        background: rgb(213,213,214);
      }

      .color:nth-child(5){
        background: rgb(179,200,175);
      }

      .color:nth-child(6){
        background: rgb(238,240,192);
      }

      .color:nth-child(7){
        background: white;
      }
    }
  }

  @media all and (max-width: 1100px) {
    flex-wrap: wrap;

    .left, .right {
      width: 100%;
    }
  }
}