@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #121213;
  font-family: 'Roboto Slab', serif; }

.logos {
  height: 100px;
  border-bottom: 1px solid #333;
  margin-bottom: 50px;
  display: flex;
  justify-content: space-around; }

.topHeader {
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid #333; }
  .topHeader .insideSlider {
    height: 50vh; }
    .topHeader .insideSlider .item {
      width: 100%;
      height: 50vh;
      background: black; }
      .topHeader .insideSlider .item video {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 50%;
        top: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%); }
  .topHeader .overlay {
    color: white;
    position: absolute;
    bottom: 20px;
    font-size: 1.75em;
    width: calc(100% - 40px);
    text-align: center;
    padding: 20px; }

.content {
  display: flex;
  margin-bottom: 10vh; }
  .content .left, .content .right {
    width: 50%;
    color: white;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    line-height: 25pt;
    font-size: 1.25em;
    letter-spacing: 0.5pt;
    word-spacing: 3pt; }
    .content .left p, .content .left ul, .content .right p, .content .right ul {
      max-width: 600px;
      margin: 20px;
      margin-top: 0; }
      .content .left p a, .content .left ul a, .content .right p a, .content .right ul a {
        color: white;
        text-decoration: none;
        background: #333;
        padding-left: 10px;
        padding-right: 10px; }
        .content .left p a:hover, .content .left ul a:hover, .content .right p a:hover, .content .right ul a:hover {
          background: white;
          color: #333; }
  .content .right {
    position: relative; }
    .content .right .canvas, .content .right .output {
      background: #333;
      width: 256px;
      height: 256px;
      display: inline-block;
      margin: 5px;
      position: relative; }
    .content .right .inside.hidden {
      display: none; }
    .content .right .loading {
      font-size: 0.75em;
      color: #999; }
    .content .right .info {
      text-align: left;
      margin-bottom: 50px; }
    .content .right .inside {
      text-align: center;
      max-width: 600px;
      width: 100%; }
    .content .right .nav {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 5px;
      margin-bottom: 50px; }
      .content .right .nav .itemBlock {
        text-transform: uppercase;
        font-size: 0.75em;
        text-align: center;
        background: transparent;
        color: white;
        border: 1px solid white;
        cursor: pointer; }
        .content .right .nav .itemBlock:hover {
          background: #333;
          cursor: pointer; }
        .content .right .nav .itemBlock.active {
          background: #fcfcfc;
          color: #333; }
  .content .paleta {
    position: absolute;
    width: 20px;
    left: -35px; }
    .content .paleta .color {
      width: 20px;
      height: 20px;
      margin-bottom: 5px;
      border-radius: 40px;
      background: white;
      cursor: pointer;
      border: 3px solid white;
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
      -webkit-transition: 0.2s all ease;
      transition: 0.2s all ease; }
      .content .paleta .color.active {
        -webkit-transform: translateX(-15px);
                transform: translateX(-15px); }
    .content .paleta.luna .color:nth-child(1) {
      background: red; }
    .content .paleta.luna .color:nth-child(2) {
      background: lime; }
    .content .paleta.luna .color:nth-child(3) {
      background: blue; }
    .content .paleta.luna .color:nth-child(4) {
      background: black; }
    .content .paleta.topo .color:nth-child(1) {
      background: #8eb5db; }
    .content .paleta.topo .color:nth-child(2) {
      background: #d4c5b8; }
    .content .paleta.topo .color:nth-child(3) {
      background: #efdfc1; }
    .content .paleta.topo .color:nth-child(4) {
      background: #d5d5d6; }
    .content .paleta.topo .color:nth-child(5) {
      background: #b3c8af; }
    .content .paleta.topo .color:nth-child(6) {
      background: #eef0c0; }
    .content .paleta.topo .color:nth-child(7) {
      background: white; }
  @media all and (max-width: 1100px) {
    .content {
      flex-wrap: wrap; }
      .content .left, .content .right {
        width: 100%; } }

